import { createReducerContext } from "react-use";
import { Omr친deI } from "../data/types";

type Action =
  | { type: "velgOmr친de"; omr친de: Omr친deI }
  | { type: "velgSide"; side: string }
  | { type: "clear" };

interface State {
  omr친de?: Omr친deI;
  side?: string;
  state: "forside" | "omr친de" | "side";
}

const initialState: State = {
  omr친de: undefined,
  side: undefined,
  state: "forside",
};

const reducer = (state: State, action: Action): State => {
  switch (action.type) {
    case "velgOmr친de":
      return {
        ...state,
        omr친de: action.omr친de,
        side: undefined,
        state: "omr친de",
      };
    case "velgSide":
      return {
        ...state,
        side: action.side,
        state: "side",
      };
    case "clear":
      return {
        ...initialState,
      };
    default:
      console.error("unhandled action:", action);
      return state;
  }
};

export const [useAppContext, AppContextProvider] = createReducerContext(
  reducer,
  initialState
);
